/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

.container {
  width: 100%;
  max-width: 1625px;
  margin: 0 auto;
}
.container .row {
  display: flex;
  flex-wrap: wrap;
}
.container [class*=col-] {
  min-height: 1px;
  width: 8.3333333333%;
  background-color: aquamarine;
  border: 1px solid salmon;
  padding: 1em;
}
.container .col-1 {
  width: 8.3333333333%;
}
.container .col-2 {
  width: 16.6666666667%;
}
.container .col-3 {
  width: 25%;
}
.container .col-4 {
  width: 33.3333333333%;
}
.container .col-5 {
  width: 41.6666666667%;
}
.container .col-6 {
  width: 50%;
}
.container .col-7 {
  width: 58.3333333333%;
}
.container .col-8 {
  width: 66.6666666667%;
}
.container .col-9 {
  width: 75%;
}
.container .col-10 {
  width: 83.3333333333%;
}
.container .col-11 {
  width: 91.6666666667%;
}
.container .col-12 {
  width: 100%;
}
@media (max-width: 1352px) {
  .container [class*=col-] {
    width: 100%;
  }
}